@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
/* CSS HEX 
--columbia-blue: #D2E5EE;
--silver-metallic: #afaab9ff;
--maize-crayola: #f5cb5cff;
--oxford-blue: #102542ff;
--celadon-blue: #3d80a4ff;
*/
*{
    font-family: 'Montserrat', sans-serif;
}
.nolonoloPaddingContent{
    padding-top: 200px;
}
.nolonoloBackground{
   background-color: #F4F7F6; 
}
.nolonoloBackgroundNav{
    background-color: #2c3531;
}
.nolonoloBackgroundNavSecond{
    background-color: #2f4454;
}
.nolonoloBackgroundProductListing{
    background-color: white;
}
.nolonoloWhiteText{
    color:white;
}
.nolonoloYellowBackground{
    background-color:#ff9b42;
}
.nolonoloYellowText{
    color:#ff9b42;
}
.nolonoloNavLogo img{
    width: 100%;
}
.nolonoloListingImgSize{
    object-fit: cover ;
}
.nolonoloFullWidth{
    width: 100%;
    max-width: 100%;
}

#nav-tab{
    border-color: #2f4454 !important;
}
.nav-link{
    border-color: #2f4454 !important;
}
.nav-link:hover{
    color:white;
    background-color: #ff9b42;
    border-color: #ff9b42 !important;
}
button.nav-link.active{
    color: white !important;
    background-color: #ff9b42 !important;
    border-color: #ff9b42 !important;
}
.tab-border{
    border-color:#ff9b42 !important ;
    border-color: #ff9b42 !important;
}

.nolonoloNoDecorationLink{
    text-decoration:none;
    color:black;
}
.nolonoloNoDecorationLink:hover{
    color:#ff9b42;
}
.nolonoloNoDecorationLink img:hover{
    border-color: #ff9b42;
    border-width: 10%;
    border-style: solid;
}
.nolonoloBoldText{
    font-weight: bold;
}




.card {
    width: 300px;
    margin:20px;
    text-align: center;
    height: 400px;
  }
  .card img{
      object-fit: cover;
      height: 100%;
  }
  
  .price {
    color: grey;
    font-size: 22px;
  }
  
  .card button {
    border: none;
    outline: 0;
    padding: 12px;
    color: white;
    background-color: #2f4454;
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
  }
  
  .card button:hover {
    opacity: 0.7;
  }
.navbar-toggler-icon{
  color: #ff9b42 !important; 
}
.listingImgContainer{
  height: 65%;
  display:inline-block;
  position:relative;
}
.listingDisabledBadge{
  position: absolute;
  height:10%;
}
.isDisabled{
  display: none;
}
/* Customer Profile */

.avatarImg{
  width:100%;
  height:100%;
  object-fit:cover
}
.avatarDiv{
  width:300px;
  height:300px;
  align-self: center;
  justify-self: center;
}

@media only screen and (min-width: 992px) {  
 #nav-tab{    
    flex-direction : row !important;  
  }  
  .navTabContainer {
    order : 2;   
  }  
  .searchBarContainer {
    order : 1;
  } 
  #listingsContainer{
    flex-direction : row !important;
    justify-content: center; 
    flex-wrap: wrap;
  }
}
